/* part 2 ------------------------------------------------------------------------------------*/
.home-first-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh; /* Makes it full screen */
  margin-top: 0px; /* Remove margin to fill the screen height */
}

.first-section-first-div {
  width: 50%; /* Adjust width to match the second section */
  padding: 20px;
  box-sizing: border-box;
  margin-left: 0px; /* Ensure left margin is not affecting the flexbox layout */
}

.sliders-component {
  width: 50%; /* Match the width of the first section */
  padding: 20px;
  box-sizing: border-box;
  text-align: center; /* Adjust for center alignment */
  justify-content: center;
  align-self: center;
}
.first-section-first-div button {
  color: #fff;
  background: linear-gradient(270deg, rgba(93, 196, 184, 0.75), #41b2a5);
  cursor: pointer;
  line-height: 1.5;
  text-align: center;
  display: inline-block;
  font-weight: 500;
  padding: 10px 25px;
  border: 0;
  border-radius: 6px;
  text-transform: capitalize;
  font-size: 1.33333rem;
}
